@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
    scroll-behavior: smooth;
  }

  body {
    @apply bg-white text-gray-900 antialiased;
  }

  h1, h2, h3, h4, h5, h6 {
    @apply font-light text-gray-900;
  }

  a {
    @apply text-gray-900 hover:text-gray-700 transition-colors duration-300;
  }

  p {
    @apply text-gray-600 leading-relaxed;
  }
}

@layer components {
  .btn {
    @apply px-8 py-4 font-light text-base transition-all duration-300;
  }

  .btn-primary {
    @apply bg-gray-900 text-white hover:bg-gray-800;
  }

  .btn-secondary {
    @apply bg-white text-gray-900 border border-gray-200 hover:border-gray-300;
  }

  .section {
    @apply py-32;
  }

  .container {
    @apply max-w-7xl mx-auto px-4;
  }

  .heading {
    @apply text-5xl font-light mb-6;
  }

  .subheading {
    @apply text-xl text-gray-600 max-w-2xl mx-auto leading-relaxed;
  }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  @apply bg-gray-50;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-300 hover:bg-gray-400 transition-colors duration-300;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fade-in {
  animation: fadeIn 0.6s ease-out forwards;
}
